.loader {
  position: fixed;
  inset: 0;
  background-color: rgba(18, 18, 18, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 11;

  &__spinner {
    border: 0.25rem solid var(--white);
    border-radius: 50%;
    border-top: 0.25rem solid var(--black);
    width: 2rem;
    height: 2rem;
    animation: spin 0.5s linear infinite;
    text-indent: -9999px;
    white-space: nowrap;
  }

  @media (prefers-reduced-motion) {
    &__spinner {
      border: none;
      border-radius: 0;
      width: auto;
      height: auto;
      animation: none;
      background-color: white;
      padding: 0.5em 1em;
      position: relative;
      text-indent: 0;
      white-space: normal;
    }
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
